html {
  scroll-behavior: smooth;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  overflow-y: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
}
html::-webkit-scrollbar {
  /* WebKit */
  width: 0;
  height: 0;
}

html,
body,
#root {
  height: 100vh;
  width: 100%;
  margin: 0;
  padding: 0;
}

.bg {
  background-image: url("./components/images/background.jpg");
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}

#parent-container {
  margin: auto;
  width: 100%;
  height: 90%;
}

#child-container {
  transform: translate(0, 15%);
  margin: auto;
  height: 50%;
  /* background-color: rgba(255, 255, 255, 0.5); */
  background-color: transparent;
  padding: 2%;
  text-align: center;
}

#profile-pic {
  height: 80%;
  border-radius: 50%;
}

#social-icon {
  display: flex;
  /* justify-content: space-between; */
  justify-content: center;
}

#social-space {
  width: 1vw;
}

/* Styling for Modal Components */
#modal-display {
  margin: auto;
  height: 75%;
  width: 75%;
}

#modal-close {
  color: black;
  float: right;
  font-size: 28px;
  font-weight: bold;
  background-color: transparent;
  border-width: 0px;
}

#modal-close:hover,
#modal-close:focus {
  color: red;
  text-decoration: none;
  cursor: pointer;
}

/* Navbar CSS, modifed CSS from W3 Schools */
.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgba(17, 44, 65, 0.5);
}

.navbar li {
  float: right;
}

.navbar li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.navbar li a:hover {
  background-color: #111;
}
